/*Styled only for mobile at the moment
will add iPad size styling too eventually */

@media screen and (max-width: 500px) {
  body {
      font-family: Arial, Helvetica, sans-serif;
      color: black;
  }
  
  .header {
      display: flex;
      flex-direction: row;
      background-color: whitesmoke;
      border-radius: 8px;
  }
  
  h1{
      margin-left: 20px;
      font-weight: 100;
  }
  
  .titleSpan {
      color: darkgreen;
      font-weight: bold;
  }
  
  
  .uberOneSelected {
      height: 50px;
      width: 100px;
      background-color: black;
      border-color: black;
      color: goldenrod;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 6px;
      margin-inline: 2px;
      margin-top: 30px;
      margin-right: 20px;
      box-shadow: 1px 2px 2px 1px goldenrod;
  }
  
  .uberOneNotSelected {
      height: 50px;
      width: 100px;
      background-color: white;
      border: solid black 1px;
      color: black;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 6px;
      margin-inline: 2px;
      margin-top: 30px;
      margin-right: 20px;
  }
  
  .options {
      display: flex;
      width: 95%;
      height: 35px;
      margin-top: 10px;
      gap: 10%;
  }
  
  .input {
      border-radius: 6px;
      font-size: 18px;
      text-align: center;
      width: 90%;
      margin-left: 5%;
      border: solid slategray 2px;
  }
  .users {
      display: flex;
      flex-direction: row;
      width: 100%;   
      gap: 10px; 
      font-size: 18px;
      margin-top: 3%;
      overflow-x: scroll;
      white-space: nowrap;
  }
  
  .user {
      height: 80px;
      min-width: 90px;
      background-color: whitesmoke;
      color: black;
      border: solid slategray 2px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 6px;
      margin-inline: 2px;
      font-size: 14px;
      box-shadow: 1px 1px 2px 0px slategray;
  }
  
  .activeUser {
      height: 80px;
      min-width: 90px;
      background-color: black;
      border-color: black;
      color: whitesmoke;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 6px;
      margin-inline: 2px;
      font-size: 16px;
      box-shadow: 1px 1px 2px 0px slategray;
  }
  
  .addToOrder{
      display: flex;
      font-size: 18px;
      background-color: whitesmoke;
      border-radius: 8px;
      margin-top: 3%;
      margin-bottom: 3%;
  }
  
  .itemValue {
      width: 33%;
      margin-top: 15px;
      margin: 3%;
      text-align: center;
      height: 30px;
      border-radius: 6px;
      font-size: 16px;
      border: solid slategray 2px;
  }
  
  .quantityInput {
      width: 33%;
      margin-top: 15px;
      margin: 3%;
      text-align: center;
      height: 30px;
      border-radius: 6px;
      font-size: 16px;
      border: solid slategray 2px;
  }
  
  p {
      text-align: center;
  }
  
  .button {
      width: 45%;
      height: 50px;
      margin: 2%;
      border-radius: 6px;
      font-size: 15px;
      border: solid slategray 2px;
      color: black;
  }
  
  .button:active {
      background-color: black;
      color: white;
  }

  .button:disabled {
    color: lightgray;
  }
  
  .button:disabled:active{
      background-color: whitesmoke;
  }
  
  .addUserButton {
      width: 25%;
      float: right;
      height: 50px;
      margin: 3%;
      margin-top: 4%;
      margin-right: 4%;
      border-radius: 6px;
      font-size: 14px;
      background-color: black;
      border-color: black;
      color: whitesmoke;
      box-shadow: 1px 1px 2px 0px slategray;
  }
  
  .addUserButton:active {
      background-color: black;
      color: white;
  }
  
  .currentUser{
      padding: 5%;
      font-size: 16px;
      background-color: whitesmoke;
      border-radius: 8px;
      margin-top: 3%;
  }
  
  .totals {
      width: 90%;
      margin-inline: 5%;
      font-size: 18px;
      padding-bottom: 20px;
  }
  
  .subtotalEntry {
      padding: 2%;
      border-bottom: solid lightgray 1px;
  }
  
  .subtotalEntry:nth-child(even){
      background-color: whitesmoke;
  }
  
  .totalEntry{
      padding: 2%;
      border-bottom: solid lightgray 1px;
      font-size: 18px;
  }
  
  .totalEntry:nth-child(odd){
      background-color: whitesmoke;
  }
  
  .totalsSpan {
      float: right;
  }
  
  
  .calculateButton {
      width: 90%;
      margin: 5%;
      height: 60px;
      border-radius: 12px;
      font-size: 16px;
      background-color: black;
      color: white;
  }
  
  .calculateButton:active {
      color: black;
      background-color: whitesmoke;
  }
  
  .grandTotal {
      font-weight: bold;
      margin-inline: 20%;
      margin-top: 5%;
      margin-bottom: 5%;
      font-size: 20px;
  }
  }